*,html{
    margin: 0;
    padding: 0;
}
body{
    
    
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.pokedex{
    background: red;
    color: white;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    width: 250px;
    height: 400px;
    border: 3px solid black;
    text-align: center;
    padding: 10px;
    
}
.pokedex__input{
    background: black;
    color: white;
    margin: 30px;
    padding: 10px;
    border: 1px solid white;
    outline: none;
    
}
.pokedex__pantalla{
    background: white;
    height: 60%;
}
.pokedex__foto{
    width: 100px;
    margin: 50px;
}